import './App.css';
import { Header, Content } from './components'
import { useEffect, useState } from 'react';
import C from './hooks/application';
import { addAccess } from './services/user/use-data';

function App() {
  const [acn, setAcn] = useState('');
  const [app, setApp] = useState('');
  const [access, setAccess] = useState(0);
  useEffect(() => {
    let _ref = '';
    if (window.location.search != undefined && window.location.search.includes('ref')) {
      _ref = window.location.search.replace("?ref=", "");
    }

    if(app && app.length > 0) {
      if (sessionStorage.getItem('userKey') !='aaaa') {
        addAccess(_ref);
      }
    } {
      setApp(navigator.language);
      if (sessionStorage.getItem('userKey') !='aaaa') {
        sessionStorage.setItem('userKey', 'aaaa');
        addAccess(_ref);
      }
    }
  }, [app])
  return (
      <C.Provider value={{ acn, setAcn }}>
        <Header />
        <Content />
      </C.Provider>
  );
}

export default App;
