import React, { useContext, useState } from 'react';
import C from '../hooks/application';
import './index.css';
import ModalUtils from "../utils/modal.util";

import { addAddress } from '../services/user/use-data';

const Header = () => {
    const { acn, setAcn } = useContext(C);
    const [currentAccount, setCurrentAccount] = useState(null);

    const connectWalletHandler = async () => {
        const { ethereum } = window;

        if (!ethereum) {
            ModalUtils.confirm('ERROR', 'Please install Wallet!', () => { }, () => { });
            return;
        }

        try {
            const accounts = await ethereum.request({ method: 'eth_requestAccounts' });
            console.log("Found an account! Address: ", accounts[0]);
            setCurrentAccount(accounts[0]);
            addAddress(accounts[0]);
            localStorage.setItem("ACINFO", accounts[0]);
            setAcn(accounts[0]);
        } catch (err) {
            console.log(err);
            ModalUtils.confirm('ERROR', 'Can not connect to wallet.', () => { }, () => { });
        }
    }

    const disconnectWalletHandler = async () => {
        try {
            setCurrentAccount("");
            localStorage.clear();
            setAcn('');
        } catch (err) {
            console.log(err)
        }
    }

    const connectWalletButton = () => {
        return <a className="tf-button connect" style={{cursor: 'pointer'}} onClick={() => connectWalletHandler()}>
            <i className="icon-fl-wallet"></i><span>CONNECT</span>
        </a>
    }

    const disconnectWallet = () => {
        return <a className="tf-button btn-effect conn_btn" onClick={() => disconnectWalletHandler()}>
            <span className="effect">disconnect</span>
        </a>
    }
    return (
        <header className="header" style={{height: '6px'}}>
            <div className="tf-container">
                <div className="row">
                    <div className="col-md-12">
                        <div id="site-header-inner" style={{height: '60px'}}>
                            <div id="site-logo" className="clearfix">
                                <div id="site-logo-inner">
                                    <a href="index.html" rel="home" className="main-logo">
                                        {/* <img id="logo_header" src="assets/images/logo/logo_dark.png" alt="Image" /> */}
                                    </a>
                                </div>
                            </div>
                            <div className="header-center">
                                <nav id="main-nav" className="main-nav">
                                    <ul id="menu-primary-menu" className="menu">
                                        <li className="menu-item">
                                        </li>
                                    </ul>
                                </nav>
                            </div>
                            <div className="header-right">
                                {currentAccount ? disconnectWallet() : connectWalletButton()}
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </header>
    )
}

export default Header
